import { Spin, Typography } from "antd";
import React from "react";
import { Item } from "../../queries/useAllItemsQuery";
import { useItemQuery } from "../../queries/useItemQuery";
import { useNewWorldContext } from "../new-world-context";

interface Props {
  item: Item | undefined;
}

export const ItemView: React.FC<Props> = ({ item }) => {
  const { selectedWorld, isWorldSelected } = useNewWorldContext();

  console.log(item, item?.id, isWorldSelected ? selectedWorld : undefined);

  const { data: itemHistory, isLoading } = useItemQuery({
    itemId: item?.id,
    worldId: isWorldSelected ? selectedWorld : undefined,
  });

  if (!isWorldSelected) {
    return (
      <div className="w-full h-full flex justify-center items-center">
        <Typography className="!text-gray-400">
          Please select a world
        </Typography>
      </div>
    );
  }

  if (!item) {
    return (
      <div className="w-full h-full flex justify-center items-center">
        <Typography className="!text-gray-400">
          Please select an item
        </Typography>
        ;
      </div>
    );
  }

  if (isLoading || !itemHistory) {
    return (
      <div className="w-full h-full flex justify-center items-center">
        <Spin
          tip={
            <Typography className="!text-gray-400">
              Fetching {item.name}
            </Typography>
          }
          delay={200}
        />
      </div>
    );
  }

  return <>{JSON.stringify(itemHistory, null, 4)}</>;
};
