import axios from "axios";
import { useQuery } from "react-query";

export interface Item {
  id: string;
  name: string;
  nameKey: string;
  descriptionKey: string;
  iconPath: string;
  type: string;
  tier: number;
  displayTier: number;
  canBeSold: boolean;
  trading: {
    category: string;
    family: string;
    group: string;
  } | null;
}

const fetchItems = async () => {
   const repsonse = await axios.get<Item[]>("https://gamestatic.new-world.exchange/items.json");
   return repsonse.data;
};

export const useAllItemsQuery = () => {
  return useQuery(["allItems"], fetchItems, {
    staleTime: Infinity,
    cacheTime: 60 * 1000 * 60 * 24,
  });
};
