import React from "react";
import Line from "../../types/Line";
import { drawLines, line } from "../../utils";
// eslint-disable-next-line no-var, @typescript-eslint/no-var-requires
var seedrandom = require("seedrandom");

type Props = {
  size: {
    x: number;
    y: number;
  };
  lines: Line[];
  lineCount: number;
  smooth: number;
  simplfy: number;
  pointDeviation: Point;
  spointDeviation: Point;
};

// eslint-disable-next-line @typescript-eslint/no-unused-vars
function makeid(length: number) {
  let result = "";
  const characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  const charactersLength = characters.length;
  for (let i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

const LineCanvas = ({
  size,
  lines,
  lineCount,
  simplfy,
  smooth,
  pointDeviation,
  spointDeviation,
}: Props) => {
  const canvasRef = React.useRef<HTMLCanvasElement>(null);
  const rng = seedrandom("hello.");

  const drawLine = (ctx: CanvasRenderingContext2D, points: Point[]) => {
    drawLines(
      ctx,
      line(points, rng, pointDeviation, spointDeviation, smooth, simplfy / 100)
    );
  };

  /*     setInterval(() => {
        rng = seedrandom(makeid(5))
        tryRender();
    }, 16)
 */
  const tryRender = () => {
    if (canvasRef.current) {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext("2d");
      if (ctx) {
        ctx.imageSmoothingEnabled = true;
        ctx.imageSmoothingQuality = "high";
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.fillStyle = "#eeeeee";
        ctx.fill();
        lines.forEach((line) => {
          for (let i = 0; i < lineCount; i++) {
            ctx.strokeStyle = line.color ?? "rgb(153, 204, 255)";
            ctx.globalCompositeOperation =
              line.blend as GlobalCompositeOperation;
            ctx.lineWidth = 3;
            drawLine(ctx, line.points);
          }
        });
      }
    }
  };

  tryRender();

  React.useEffect(() => {
    tryRender();
  }, [canvasRef.current]);

  return (
    <div className="flex items-center justify-center w-full">
      <canvas width={size.x} height={size.y} ref={canvasRef} />
    </div>
  );
};

export default React.memo(LineCanvas);
