import React from "react";
import { drawLines, spline } from "../../utils";
// eslint-disable-next-line @typescript-eslint/no-var-requires
const seedrandom = require("seedrandom");

type Props = {
  size: {
    x: number;
    y: number;
  };
  lines: Point[][];
  lineCount: number;
  smooth: number;
  simplfy: number;
  pointDeviation: Point;
  spointDeviation: Point;
  lineSplineSteps: number;
};

const SplineCanvas = ({
  size,
  lines,
  lineCount,
  simplfy,
  smooth,
  pointDeviation,
  spointDeviation,
  lineSplineSteps,
}: Props) => {
  const canvasRef = React.useRef<HTMLCanvasElement>(null);
  const rng = seedrandom("hello.");

  const drawLine = (ctx: CanvasRenderingContext2D, points: Point[]) => {
    ctx.strokeStyle = "rgb(153, 204, 255)";
    ctx.lineWidth = 4;
    ctx.fillStyle = "rgb(153, 204, 255)";
    const splinePoints = spline(
      points,
      rng,
      pointDeviation,
      spointDeviation,
      lineSplineSteps,
      smooth,
      simplfy / 100
    );
    //const sandInterpol = applySandInterpol(rng);
    drawLines(ctx, splinePoints);
  };

  const tryRender = () => {
    if (canvasRef.current) {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext("2d");
      if (ctx) {
        ctx.imageSmoothingEnabled = true;
        ctx.globalCompositeOperation = "multiply";
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
        lines.forEach((line) => {
          for (let i = 0; i < lineCount; i++) {
            drawLine(ctx, line);
          }
        });
      }
    }
  };

  tryRender();

  React.useEffect(() => {
    tryRender();
  }, [canvasRef.current]);

  return (
    <div className="flex items-center justify-center w-full">
      <canvas width={size.x} height={size.y} ref={canvasRef} />
    </div>
  );
};

export default React.memo(SplineCanvas);
