import { Col, Grid, Row, Spin } from "antd";
import React from "react";
import { ItemView } from "../new-world/components/item-view";
import { ItemsList } from "../new-world/components/items-list";
import { NewWorldContextProvider } from "../new-world/components/new-world-context";
import { WorldList } from "../new-world/components/world-list";
import { useAllItems } from "../new-world/hooks/useAllItems";
import { useWorlds } from "../new-world/hooks/useWorlds";
import { Item } from "../new-world/queries/useAllItemsQuery";

const NewWorld: React.FC = () => {
  const { isLoading } = useAllItems();
  const { isLoading: worldsLoading } = useWorlds();
  const [selectedItem, setSelectedItem] = React.useState<Item | undefined>();

  if (isLoading || worldsLoading) {
    return (
      <div className="inset-y-1/2 inset-x-1/2 absolute">
        <Spin />
      </div>
    );
  }

  const hanldeItemSelect = (item: Item) => {
    setSelectedItem(item);
  };

  return (
    <NewWorldContextProvider>
      <div>
        <WorldList />
        <Row wrap>
          <Col span={8}>
            <ItemsList
              onItemChange={hanldeItemSelect}
              selectedItem={selectedItem}
            />
          </Col>
          <Col span={16}>
            <ItemView item={selectedItem} />
          </Col>
        </Row>
      </div>
    </NewWorldContextProvider>
  );
};

export default NewWorld;
