import axios from "axios";
import { useQuery } from "react-query";

export interface World {
  nw_id: string;
  name: string;
  region: string;
  status: "MERGED" | "ACTIVE";
}

export interface WorldResponse {
  timestamp: string;
  next: string;
  previous: string;
  results: World[];
}

const fetchWorlds = async () => {
  const repsonse = await axios.get<WorldResponse>(
    "https://api.new-world.exchange/api/v1/world/"
  );
  return repsonse.data;
};

export const useWorldsQuery = () => {
  return useQuery(["allWorlds"], fetchWorlds, {
    staleTime: Infinity,
    cacheTime: 60 * 1000 * 60 * 24,
  });
};
