import { Row, Col } from 'antd'
import React from 'react'
import LineCanvas from '../components/line-canvas/LineCanvas'
import { usePoint } from '../components/usePoint'
import { Range, useRange } from '../components/useRange'
import Line from '../types/Line'

const grid = (h: number, w: number, rows: number, cols: number) => {
    const lines: Line[] = []

    const xStep = h / rows;
    const yStep = w / cols;
    for (let i = 1; i < rows; i++) {
        lines.push(new Line(
            [
                {
                    x: Math.floor(xStep * i),
                    y: 0
                },
                {
                    x: Math.floor(xStep * i),
                    y: h,
                }
            ]
        ));
    }

    for (let i = 1; i < cols; i++) {
        lines.push(new Line([
            {
                x: 0,
                y: Math.floor(yStep * i),
            },
            {
                x: h,
                y: Math.floor(yStep * i)
            }
        ]));
    }


    return lines;
}

const LinePage = () => {

    const [lineCount, LineRange] = useRange(30, 1, 30, "Line Count");
    // const [lineSplineSteps, SplineRange] = useRange(13, 1, 30, "Spline Step Size");
    const [smooth, SmoothRange] = useRange(2, 0, 8, "Chaikin smooth iterations");
    const [simplfy, SimplifyRange] = useRange(0, 0, 100, "Simplify Cooficiant");
    const [pointDeviation, [pdx, pdy]] = usePoint({ x: 25, y: 88 }, 0, 2000, "Point deviation");
    const [spointDeviation, [spdx, spdy]] = usePoint({ x: 141, y: 85 }, 0, 2000, "Sub-Point deviation");

    const cGrid = React.useMemo(() => grid(1200, 800, 8, 12), []);
    return (
        <>
            <LineCanvas size={{
                x: 1200,
                y: 800,
            }}
                lines={cGrid}
                lineCount={lineCount}
                smooth={smooth}
                simplfy={simplfy}
                pointDeviation={pointDeviation}
                spointDeviation={spointDeviation}
            />
            <Row>
                <Col span={8}>
                    <Range {...LineRange} />
                </Col>
                <Col span={8}>
                    <Range {...SmoothRange} />
                </Col>
                <Col span={8}>
                    <Range {...SimplifyRange} />
                </Col>
                <Col span={8}>
                    <Range {...pdx} />
                    <Range {...pdy} />
                </Col>
                <Col span={8}>
                    <Range {...spdx} />
                    <Range {...spdy} />
                </Col>
            </Row>
        </>
    )
}

export default LinePage