import axios from "axios";
import { useQuery } from "react-query";

export interface ItemHistory {
  timestamp: string;
  world_id: string;
  item_id: string;
  sell_total_contracts: number;
  sell_total_supply: number;
  sell_lot_size: number;
  sell_lot_unit_price: number;
  buy_total_contracts: number;
  buy_total_supply: number;
  buy_lot_size: number;
  buy_lot_unit_price: number;
}

interface RequestData {
  itemId: string | undefined;
  worldId: string | undefined;
}

const fetchWorlds =
  ({ itemId, worldId }: RequestData) =>
  async () => {
    const repsonse = await axios.get<ItemHistory[]>(
      `https://api.new-world.exchange/api/v2/item/history/?item=${itemId}&world=${worldId}`
    );
    return repsonse.data;
  };

export const useItemQuery = (requestData: RequestData) => {
  console.log(requestData);
  return useQuery(
    ["item", requestData.worldId, requestData.itemId],
    fetchWorlds(requestData),
    {
      staleTime: 60 * 1000 * 10,
      enabled:
        requestData.worldId !== undefined && requestData.itemId !== undefined,
    }
  );
};
