import { Typography, Slider, SliderSingleProps } from "antd";
import React from "react";

export const useRange = (initialValue: number, min: number, max: number, label: string,): [number, IRangeProps] => {

    const [value, setValue] = React.useState(initialValue);

    const onChange = (val: number) => setValue(val);

    const props: IRangeProps = {
        min,
        max,
        label,
        defaultValue: value,
        onChange,

    }
    return [value, props]
}

export interface IRangeProps extends SliderSingleProps {
    label: string;
}

export const Range: React.FC<IRangeProps> = ({ label, ...props }) => {
    return <>
        <Typography.Text>{label}</Typography.Text>
        <Slider {...props} />
    </>
}