import React from "react";
import "./App.css";
import { Link, Outlet } from "react-router-dom";
import { Divider, Layout, Menu, Typography } from "antd";
import { Content } from "antd/lib/layout/layout";
import { CloudOutlined } from "@ant-design/icons";
import Sider from "antd/lib/layout/Sider";
import { QueryClient, QueryClientProvider } from "react-query";

const queryClient = new QueryClient();

function App() {
  const url = "/" + window.location.href.split("/").pop();

  return (
    <QueryClientProvider client={queryClient}>
      <Layout hasSider className="bg-dark">
        <Sider
          style={{
            overflow: "auto",
            height: "100vh",
            position: "fixed",
            left: 0,
            top: 0,
            bottom: 0,
          }}
          theme="dark"
        >
          <div className="logo" />
          <Menu theme="dark" mode="inline" defaultSelectedKeys={[url ?? "/"]} openKeys={['genArt']}>
            <Divider />
            <Menu.Item key="/">
              <CloudOutlined />
              <Link to="/"> Home</Link>
            </Menu.Item>
            <Menu.SubMenu title="Generative Art" level={0} key="genArt" >
              <Menu.Item key="/line">
                <CloudOutlined />
                <Link to="/line"> Line</Link>
              </Menu.Item>
              <Menu.Item key="/spline">
                <CloudOutlined />
                <Link to="/spline"> Spline</Link>
              </Menu.Item>
              <Menu.Item key="/sun">
                <CloudOutlined />
                <Link to="/sun"> Sun</Link>
              </Menu.Item>
              <Menu.Item key="/vector">
                <CloudOutlined />
                <Link to="/vector"> Vector</Link>
              </Menu.Item>
            </Menu.SubMenu>
            <Menu.Item key="/nw" disabled>
              <CloudOutlined />
              <Link to="/nw"> New world tracker</Link>
            </Menu.Item>
          </Menu>
        </Sider>
        <Layout className="site-layout bg-dark" style={{ marginLeft: 200 }}>
          <Content
            className="bg-dark"
            style={{ margin: "24px 16px 0", overflow: "initial" }}
          >
            <div
              className="site-layout-background dark"
              style={{ padding: 24 }}
            >
              <Outlet />
            </div>
          </Content>
        </Layout>
      </Layout>
    </QueryClientProvider>
  );
}

export default App;
