import React from "react";
import { IRangeProps } from "./useRange";

export const usePoint = (initalPoint: Point, min: number, max: number, label: string, step: number = 1): [Point, IRangeProps[]] => {



    const [valueX, setValueX] = React.useState(initalPoint.x);
    const [valueY, setValueY] = React.useState(initalPoint.y);

    const onChangeX = (val: number) => setValueX(val);
    const onChangeY = (val: number) => setValueY(val);


    console.log(valueX, valueY);

    const propsX: IRangeProps = {
        min,
        max,
        label: label + " X",
        defaultValue: valueX,
        step,
        onChange: onChangeX,

    }

    const propsY: IRangeProps = {
        min,
        max,
        label: label + " Y",
        defaultValue: valueX,
        step,
        onChange: onChangeY,

    }
    return [{
        x: valueX,
        y: valueY,
    }, [propsX, propsY]]
}