import { useWorldsQuery } from "../queries/useWorldsQuery";

export const useWorlds = () => {
  const { data: worldsResponse, isLoading } = useWorldsQuery();

  return {
    worlds: worldsResponse ? worldsResponse.results : [],
    active: worldsResponse
      ? worldsResponse.results.filter((world) => world.status === "ACTIVE")
      : [],
    merged: worldsResponse
      ? worldsResponse.results.filter((world) => world.status === "MERGED")
      : [],
    isLoading,
  };
};
