import { Col, Row } from "antd";
import React, { useState } from "react";
import { CheckBox, useCheckBox } from "../components/useCheckBox";
import { usePoint } from "../components/usePoint";
import { Range, useRange } from "../components/useRange";
import VectorCanvas from "../components/vector-canvas/VectorCanvas";
import {
  generateVector2DField,
  tracePath,
} from "../components/vector-canvas/vectorFieldHelpers";
import { Vector2D } from "../types/Vector2D";

const seedrandom: Seedrandom = require("seedrandom");

const seed = "hello.";

const VectorPage = () => {
  const [randomfn] = useState(() => seedrandom(seed));

  const size = React.useMemo(
    () => ({
      x: 1200,
      y: 800,
    }),
    []
  );

  const [velocity, [startXprops, startYProps]] = usePoint(
    { x: 1, y: 1 },
    -100,
    100,
    "Start Velocity",
    0.1
  );

 

  const [stepSize, VectorFieldSize] = useRange(5, 5, 100, "Vector Field Size");
  const [noiseScale, NoiseScale] = useRange(68, 5, 100, "Noise Scale");
  const [fieldStrenght, FieldStrenght] = useRange(17, 1, 100, "Field Strenght");
  const [lineCount, LineCount] = useRange(20000, 1, 50000, "Line Count");
  const [isDebugMode, DebugModeProps] = useCheckBox(false, "Debug mode");
  const forceField = React.useMemo(
    () => generateVector2DField(size, noiseScale, stepSize, seed),
    [noiseScale, size, stepSize]
  );

  const particlePath: Vector2D[][] = React.useMemo(() => {
    const start = performance.now();
    const paths = [];
    for (let i = 0; i < lineCount; i++) {
      const path = tracePath(
        forceField,
        new Vector2D({
          ///x: (size.x / lineCount) * i,
          x: randomfn() * size.x,
          y: randomfn() * size.y,
        }),
        new Vector2D({
          x: 0,
          y: 0,
        }),
        stepSize,
        fieldStrenght
      );
      paths.push(path);
    }

    const end = performance.now();
    console.log(`Execution time: ${end - start} ms`);

    return paths;
  }, [forceField, size.x, size.y, randomfn, stepSize, fieldStrenght, lineCount]);

  return (
    <>
      <VectorCanvas
        size={size}
        vectors={forceField}
        paths={particlePath}
        stepSize={stepSize}
        debug={isDebugMode}
      />
      <Row>
        <Col span={8}>
          <Range {...startXprops} />
          <Range {...startYProps} />
        </Col>
        <Col span={8}>
          <Range {...VectorFieldSize} />
        </Col>
        <Col span={8}>
          <Range {...NoiseScale} />
        </Col>
        <Col span={8}>
          <Range {...FieldStrenght} />
        </Col>
        <Col span={8}>
          <Range {...LineCount} />
        </Col>
        <Col span={8}>
          <CheckBox {...DebugModeProps} />
        </Col>
      </Row>
    </>
  );
};

export default VectorPage;
