import { useAllItemsQuery } from "../queries/useAllItemsQuery";

export const useAllItems = () => {
  const { data: items = [], isLoading } = useAllItemsQuery();

  const getItembyId = (id: string) => items.find((item) => item.id === id);

  return {
    items,
    getItembyId,
    isLoading,
  };
};
