import React from "react";
import { Vector2D } from "../../types/Vector2D";
import { drawLines, Vec2d2RGB } from "../../utils";

type Props = {
  size: {
    x: number;
    y: number;
  };
  vectors: [Vector2D[][], number[][]];
  paths: Vector2D[][];
  stepSize: number;
  debug?: boolean;
};

const VectorCanvas = ({ size, vectors, paths, stepSize, debug }: Props) => {
  const canvasRef = React.useRef<HTMLCanvasElement>(null);

  const [dir, str] = vectors;

  const drawLine = (ctx: CanvasRenderingContext2D, points: Point[]) => {
    drawLines(ctx, points);
  };

  const drawDebug = (ctx: CanvasRenderingContext2D) => {
    ctx.lineWidth = 1;
    for (var x = 0; x < dir.length; x++) {
      for (var y = 0; y < dir[x].length; y++) {
        const startPos = {
          x: stepSize * x,
          y: stepSize * y,
        };

        const endPos = dir[x][y]
          .scaleBy((stepSize / 2) * str[x][y])
          .add(startPos);
        const angle = dir[x][y].getAngle();
        ctx.strokeStyle = Vec2d2RGB(dir[x][y], str[x][y] + 1 / 2);
        drawLine(ctx, [startPos, endPos]);
        ctx.beginPath();
        ctx.arc(
          startPos.x,
          startPos.y,
          stepSize / 2,
          angle - Math.PI / 8,
          angle + Math.PI / 8
        );
        ctx.stroke();
      }
    }
  };

  const tryRender = React.useCallback(() => {
    if (canvasRef.current) {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext("2d");
      if (ctx) {
        ctx.imageSmoothingEnabled = true;
        ctx.imageSmoothingQuality = "high";
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.fillStyle = "#eeeeee";
        ctx.fill();

        ctx.strokeStyle = "rgba(0, 0, 0, 0.5)"
        ctx.lineWidth = 1;
        paths.forEach((path) => drawLine(ctx, path));
        if (debug) {
          drawDebug(ctx);
        }
      }
    }
  }, [debug, paths, stepSize, dir]);

  React.useEffect(() => {
    if (canvasRef.current) {
      tryRender();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [canvasRef.current, tryRender]);

  return (
    <div className="flex items-center justify-center w-full">
      <canvas width={size.x} height={size.y} ref={canvasRef} />
    </div>
  );
};

export default React.memo(VectorCanvas);
