import Spline from "typescript-cubic-spline";
import { Vector2D } from "../types/Vector2D";
import { simplify } from "./simplify";

var seedrandom = require("seedrandom");

export const makeSplineFromPoint = (points: Point[]) => {
  const pointsY = points.map((point) => point.y);
  const pointsX = points.map((point) => point.x);
  return new Spline(pointsX, pointsY);
};

function pixelArtLine(x1: number, y1: number, x2: number, y2: number) {
  x1 = Math.round(x1);
  y1 = Math.round(y1);
  x2 = Math.round(x2);
  y2 = Math.round(y2);
  const dx = Math.abs(x2 - x1);
  const sx = x1 < x2 ? 1 : -1;
  const dy = -Math.abs(y2 - y1);
  const sy = y1 < y2 ? 1 : -1;
  var e2,
    er = dx + dy,
    end = false;
  const points: Point[] = [];
  while (!end) {
    points.push({
      x: x1,
      y: y1,
    });
    if (x1 === x2 && y1 === y2) {
      end = true;
    } else {
      e2 = 2 * er;
      if (e2 > dy) {
        er += dy;
        x1 += sx;
      }
      if (e2 < dx) {
        er += dx;
        y1 += sy;
      }
    }
  }
  return points;
}

export const splineToPoints = (spline: Spline, stepSize: number) => {
  const lenght = spline.xs[0] + spline.xs[spline.xs.length - 1];
  const numSteps = lenght / stepSize;
  const points: Point[] = [];
  for (let i = 0; i < numSteps; i++) {
    const x = i * stepSize;
    points.push({
      x,
      y: spline.at(i * stepSize),
    });
  }
  return points;
};

export const drawLines = (ctx: CanvasRenderingContext2D, points: Point[]) => {
  if (points.length < 2) {
    return;
  }
  ctx.beginPath();
  ctx.moveTo(points[0].x, points[0].y);
  for (let i = 1; i < points.length; i++) {
    ctx.lineTo(points[i].x, points[i].y);
  }
  ctx.stroke();
  ctx.closePath();
};

export const drawDots = (ctx: CanvasRenderingContext2D, points: Point[]) => {
  for (let i = 0; i < points.length; i++) {
    ctx.fillRect(points[i].x, points[i].y, 2, 2);
  }
};

export const standartDeviation = (set: number[]) => {
  const sorted = set.sort();
  const meanIndex =
    sorted.length % 2 === 0 ? sorted.length / 2 : (sorted.length - 1) / 2;
  const mean = sorted[meanIndex];
  const distanceToMean = sorted.map((s) => (s - mean) * (s - mean));
  const sumOfdist = distanceToMean.reduce<number>(
    (prev, current) => prev + current,
    0
  );
  return Math.sqrt(sumOfdist);
};

export const standarDeviationPoints = (points: Point[]) => {
  const pointsY = points.map((point) => point.y);
  const pointsX = points.map((point) => point.x);

  return {
    x: standartDeviation(pointsX),
    y: standartDeviation(pointsY),
  };
};

export const chaikin = (arr: Point[], interations: number): Point[] => {
  if (interations === 0) return arr;
  const l = arr.length;
  const smooth = arr
    .map((c, i) => {
      return [
        {
          x: 0.75 * c.x + 0.25 * arr[(i + 1) % l].x,
          y: 0.75 * c.y + 0.25 * arr[(i + 1) % l].y,
        },
        {
          x: 0.25 * c.x + 0.75 * arr[(i + 1) % l].x,
          y: 0.25 * c.y + 0.75 * arr[(i + 1) % l].y,
        },
      ];
    })
    .flat();
  return interations === 1 ? smooth : chaikin(smooth, interations - 1);
};

export const applyDevTopoints =
  (rng: any) =>
  (
    points: Point[],
    dev: Point,
    cof = 0.2,
    applyForX: boolean = false
  ): Point[] => {
    const devSx = dev.x / 2;
    const devSy = dev.y / 2;
    const nPoints = [...points].map((point) => ({
      x: applyForX ? point.x + (rng() * dev.x - devSx) * cof : point.x,
      y: point.y + (rng() * dev.y - devSy) * cof,
    }));
    return nPoints;
  };

export const applyDevToSubpoints =
  (rng: any) =>
  (points: Point[], dev: Point, cof = 0.2, applyForX = false): Point[] => {
    const devSx = dev.x / 2;
    const devSy = dev.y / 2;
    const nPoints = [...points].map((point) => ({
      x: applyForX ? point.x + (rng() * dev.x - devSx) * cof : point.x,
      y: point.y + (rng() * dev.y - devSy) * cof,
    }));
    nPoints[0] = points[0];
    nPoints[nPoints.length - 1] = points[points.length - 1];
    return nPoints;
  };

export const applySandInterpol =
  (rng: any) =>
  (points: Point[], dev: Point, cof = 0.01, subPointCount = 16) => {
    const subPoints: Point[] = [];
    const devSx = dev.x / 2;
    const devSy = dev.y / 2;
    for (let i = 0; i < points.length; i++) {
      const point = points[i];
      subPoints.push(point);
      for (let c = 0; c < subPointCount; c++) {
        subPoints.push({
          x: point.x + (rng() * dev.x - devSx) * cof,
          y: point.y + (rng() * dev.y - devSy) * cof,
        });
      }
    }
    return subPoints;
  };

export const line = (
  points: Point[],
  rnd: any = new seedrandom("hello."),
  pointDeviation: Point = { x: 10, y: 10 },
  spointDeviation: Point = { x: 10, y: 10 },
  smoothIterations = 2,
  smoothTolerance = 0.8
) => {
  const devPntFn = applyDevTopoints(rnd);
  const devSubPtnFn = applyDevToSubpoints(rnd);
  const devPoints = devPntFn(points, pointDeviation, 0.2, true);
  const subPoints = pixelArtLine(
    devPoints[0].x,
    devPoints[0].y,
    devPoints[1].x,
    devPoints[1].y
  ).filter((p, i) => i % 4 === 0);
  const simplifiedPoints = simplify(subPoints, smoothTolerance, false);
  const devSubPoints = devSubPtnFn(
    simplifiedPoints,
    spointDeviation,
    0.2,
    true
  );
  // TODO FIX ENDS
  const smoothedPoints = chaikin(devSubPoints, smoothIterations);
  if (smoothIterations > 0) {
    smoothedPoints.splice(-2 * (smoothIterations * smoothIterations));
  }

  return smoothedPoints;
};

export const spline = (
  points: Point[],
  rnd: any = new seedrandom("hello."),
  pointDeviation: Point = { x: 10, y: 10 },
  spointDeviation: Point = { x: 10, y: 10 },
  lineSplineSteps = 10,
  smoothIterations = 2,
  smoothTolerance = 0.8
) => {
  const devPntFn = applyDevTopoints(rnd);
  const devSubPtnFn = applyDevToSubpoints(rnd);
  const devPoints = devPntFn(points, pointDeviation, 0.2, true);
  const spline = makeSplineFromPoint(devPoints);
  const subPoints = splineToPoints(spline, lineSplineSteps);
  const devSubPoints = devSubPtnFn(subPoints, spointDeviation, 0.2, true);
  const smoothedPoints = chaikin(devSubPoints, smoothIterations);
  if (smoothIterations > 0) {
    smoothedPoints.splice(-2 * smoothIterations * smoothIterations);
  }
  const simplifiedPoints = simplify(smoothedPoints, smoothTolerance, true);
  return simplifiedPoints;
};

export const Vec2d2RGB = (vec: Vector2D, multi: number) => {
  const vecR = new Vector2D({ x: 1, y: 1 / 3 ** 0.5 }).normalize();
  const vecG = new Vector2D({ x: -1, y: 1 / 3 ** 0.5 }).normalize();
  const vecB = new Vector2D({ x: 0, y: -1 }).normalize();

  const r = Math.max(3 ** 0.5 - vec.subtract(vecR).length(), 0) / 3 ** 0.5;
  const g = Math.max(3 ** 0.5 - vec.subtract(vecG).length(), 0) / 3 ** 0.5;
  const b = Math.max(3 ** 0.5 - vec.subtract(vecB).length(), 0) / 3 ** 0.5;
  return `rgba(${r * 255},${g * 255},${b * 255}, ${multi})`;
};
