import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter, HashRouter, Route, Routes } from 'react-router-dom';
import LinePage from './pages/LinePage';
import SplinePage from './pages/SplinePage';
import SunPage from './pages/SunPage';
import NewWorld from './pages/NewWorld';
import VectorPage from './pages/VectorField';

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);

root.render(
  <React.StrictMode>
    <HashRouter>
      <Routes>
        <Route path="/" element={<App />}>
          <Route path="line" element={<LinePage />} />
          <Route path="spline" element={<SplinePage />} />
          <Route path="sun" element={<SunPage />} />
          <Route path="vector" element={<VectorPage />} />
          <Route path="nw" element={<NewWorld />} />
        </Route>
      </Routes>
    </HashRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
