import { createNoise2D, RandomFn } from "simplex-noise";
import { Vector2D } from "../../types/Vector2D";
const seedrandom = require("seedrandom");

export const generateVector2DField = (
  size: Point,
  noiseScale: number,
  stepSize: number,
  seed: string
): [Vector2D[][], number[][]] => {
  const vec: Vector2D[][] = [];
  const strenght: number[][] = [];

  const rngX = seedrandom(seed + "X") as RandomFn;
  const rngY = seedrandom(seed + "Y") as RandomFn;
  const rngStr = seedrandom(seed + "STR") as RandomFn;

  const noiseX = createNoise2D(rngX);
  const noiseY = createNoise2D(rngY);
  const noiseStr = createNoise2D(rngStr);

  for (let x = 0; x < size.x / stepSize; x += 1) {
    const row: Vector2D[] = [];
    const rowStrenght: number[] = [];
    for (let y = 0; y < size.y / stepSize; y += 1) {
      row.push(
        new Vector2D({
          x: noiseX(x / noiseScale, y / noiseScale),
          y: noiseY(x / noiseScale, y / noiseScale),
        }).normalize()
      );
      rowStrenght.push((noiseStr(x / noiseScale, y / noiseScale) + 1) / 2);
    }
    vec.push(row);
    strenght.push(rowStrenght);
  }
  return [vec, strenght];
};

export const tracePath = (
  [vectorField, strenght]: [Vector2D[][], number[][]],
  startPos: Vector2D,
  startVelocity: Vector2D,
  stepSize: number,
  fieldStrenght: number = 1
): Vector2D[] => {
  let vel = new Vector2D({ x: startVelocity.x, y: startVelocity.y });
  const traced = [startPos];

  const position = [
    Math.floor(startPos.x / stepSize),
    Math.floor(startPos.y / stepSize) - 1,
  ];

  let vecFieldSample: Vector2D | null = vectorField[position[0]][position[1]];
  let vecFieldStrSample: number | null = strenght[position[0]][position[1]];

  let count = 0;

  const timeStep = 0.1;

  let len = 0;

  const dumpVec3 = new Vector2D({ x: 0, y: 0 });

  while (vecFieldSample && count < 15 && len < 800) {
    // Velocity change
    dumpVec3.x = vecFieldSample.x;
    dumpVec3.y = vecFieldSample.y;
    dumpVec3.scaleByFast(fieldStrenght * vecFieldStrSample * timeStep);
    vel.addFast(dumpVec3);


    const lenFast = vel.x * vel.x + vel.y * vel.y;

    dumpVec3.x = vel.x;
    dumpVec3.y = vel.y;
    // Air resistance
    const air = dumpVec3
      .normalizeFaster()
      .scaleByFast(-1 * lenFast ** 3 * 0.025 * timeStep);

    vel.addFast(air);

    // Update Position
    startPos = startPos.add(vel);

    len += lenFast;

    // save new position
    traced.push(startPos);

    // Update next node;

    const sampleLocation = {
      x: Math.floor(startPos.x / stepSize),
      y: Math.floor(startPos.y / stepSize),
    };

    if (
      sampleLocation.x < vectorField.length &&
      sampleLocation.x > 0 &&
      sampleLocation.y > 0 &&
      sampleLocation.y < vectorField[sampleLocation.x].length
    ) {
      vecFieldSample = vectorField[sampleLocation.x][sampleLocation.y];
    } else {
      vecFieldSample = null;
    }

    count++;
  }
  return traced;
};
