import React from "react";

import { Row, Col } from "antd";
import SplineCanvas from "../components/spline-canvas/SplineCanvas";
import { usePoint } from "../components/usePoint";
import { Range, useRange } from "../components/useRange";

const points: Point[][] = [
  [
    { x: 100, y: 350 },
    { x: 200, y: 300 },
    { x: 300, y: 380 },
    { x: 400, y: 420 },
    { x: 500, y: 220 },
    { x: 600, y: 115 },
    { x: 700, y: 250 },
  ],
  [
    { x: 100, y: 400 },
    { x: 200, y: 300 + 100 },
    { x: 300, y: 380 + 50 },
    { x: 400, y: 420 + 100 },
    { x: 500, y: 220 + 250 },
    { x: 600, y: 115 + 30 },
    { x: 700, y: 250 + 20 },
  ],
];

const SplinePage = () => {
  const [lineCount, LineRange] = useRange(30, 1, 30, "Line Count");
  const [lineSplineSteps, SplineRange] = useRange(
    13,
    1,
    30,
    "Spline Step Size"
  );
  const [smooth, SmoothRange] = useRange(2, 0, 8, "Chaikin smooth iterations");
  const [simplfy, SimplifyRange] = useRange(0, 0, 100, "Simplify Cooficiant");
  const [pointDeviation, [pdx, pdy]] = usePoint(
    { x: 163, y: 0 },
    0,
    2000,
    "Point deviation"
  );
  const [spointDeviation, [spdx, spdy]] = usePoint(
    { x: 120, y: 90 },
    0,
    2000,
    "Sub-Point deviation"
  );

  return (
    <>
      <SplineCanvas
        size={{
          x: 1200,
          y: 800,
        }}
        lines={points}
        lineCount={lineCount}
        smooth={smooth}
        simplfy={simplfy}
        pointDeviation={pointDeviation}
        spointDeviation={spointDeviation}
        lineSplineSteps={lineSplineSteps}
      />
      <Row>
        <Col span={8}>
          <Range {...LineRange} />
        </Col>
        <Col span={8}>
          <Range {...SmoothRange} />
        </Col>
        <Col span={8}>
          <Range {...SimplifyRange} />
        </Col>
        <Col span={8}>
          <Range {...SplineRange} />
        </Col>
        <Col span={8}>
          <Range {...pdx} />
          <Range {...pdy} />
        </Col>
        <Col span={8}>
          <Range {...spdx} />
          <Range {...spdy} />
        </Col>
      </Row>
    </>
  );
};

export default SplinePage;
