export class Vector2D implements Point {
  x: number;
  y: number;
  constructor({ x, y }: Point = { x: 0, y: 0 }) {
    this.x = x;
    this.y = y;
  }

  length() {
    return Math.hypot(this.x, this.y);
  }

  scaleBy(scalar: number) {
    return new Vector2D({
      x: this.x * scalar,
      y: this.y * scalar,
    });
  }

  scaleByFast(scalar: number) {
    this.x = this.x * scalar;
    this.y = this.y * scalar;
    return this;
  }

  addFast(other: Point) {
    this.x = this.x + other.x;
    this.y = this.y + other.y;
    return this;
  }

  normalize() {
    return this.scaleBy(1 / this.length());
  }

  normalizeFaster() {
    return this.scaleByFast(1 / this.length());
  }

  add(other: Point) {
    const nvec = this.clone();
    nvec.x += other.x;
    nvec.y += other.y;
    return nvec;
  }

  subtract(other: Point) {
    const nvec = this.clone();
    nvec.x -= other.x;
    nvec.y -= other.y;
    return nvec;
  }

  getAngle(): number {
    var Vx = this.x;
    var Vy = this.y;

    var radians;

    if (Vx || Vy) {
      radians = Math.atan2(Vy, Vx);
    } else {
      radians = 0;
    }

    if (radians < 0) {
      radians += 2 * Math.PI;
    }

    return radians;
  }

  clone() {
    return new Vector2D({ x: this.x, y: this.y });
  }
}
