import { Input, Pagination, Typography } from "antd";
import React, { useState } from "react";
import { useAllItems } from "../../hooks/useAllItems";
import { Item } from "../../queries/useAllItemsQuery";

interface Props {
  onItemChange: (item: Item) => void;
  selectedItem: Item | undefined;
}

export const ItemsList: React.FC<Props> = ({ onItemChange, selectedItem }) => {
  const { items } = useAllItems();

  const [filter, setFilter] = useState("");
  const [page, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(25);

  const filteredData = items
    .filter((item) => item.canBeSold)
    .filter((item) =>
      item.name.toLocaleLowerCase().includes(filter.toLocaleLowerCase())
    );

  const pageData = filteredData.slice(
    (page - 1) * itemsPerPage,
    page * itemsPerPage
  );

  return (
    <div>
      <Input onChange={(e) => setFilter(e.target.value)} placeholder="Filter" />
      {pageData.map((item) => (
        <div
          key={item.id}
          onClick={() => onItemChange(item)}
          className={`flex py-1 border-b-[1px] border-b-darkerb hover:bg-darkerb transition-colors ease-in-out duration-300 pl-2 cursor-pointer ${
            item.id === selectedItem?.id ? "bg-darkerb" : ""
          }`}
        >
          <div className="w-5 h-5 mr-2">
            {item.iconPath ? (
              <img
                src={`https://gamestatic.new-world.exchange/${item.iconPath}`}
              />
            ) : null}
          </div>

          <Typography>
            {item.name} ({item.type}) {item.canBeSold}
          </Typography>
        </div>
      ))}
      <div className="mt-4 w-full flex justify-center">
        <Pagination
          defaultCurrent={0}
          onShowSizeChange={(current, size) => {
            setItemsPerPage(size);
            setCurrentPage(0);
          }}
          onChange={(page) => setCurrentPage(page)}
          total={filteredData.length}
          pageSizeOptions={[10, 25, 50]}
          pageSize={itemsPerPage}
          current={page}
          responsive
        />
      </div>
    </div>
  );
};
