import React from 'react'
import { Row, Col } from 'antd'
import LineCanvas from '../components/line-canvas/LineCanvas'
import { usePoint } from '../components/usePoint'
import { Range, useRange } from '../components/useRange'
import Line from '../types/Line'

const W = 1200;
const H = 800;

const generateFullLines = (height = 200, offset = 0, color: string, xOffset = 0, blend?: GlobalCompositeOperation) => {
    const lines: Line[] = [];

    const step = 20;
    const steps = height / step;

    for (let i = 0; i < steps; i++) {
        lines.push(new Line(
            [
                {
                    x: 0 + xOffset,
                    y: H - i * step + offset,
                }, {
                    x: W - xOffset,
                    y: H - i * step + offset
                }

            ], color, blend))
    }

    return lines
}


const SunPage: React.FC = () => {
    const [lineCount, LineRange] = useRange(30, 1, 30, "Line Count");
    // const [lineSplineSteps, SplineRange] = useRange(13, 1, 30, "Spline Step Size");
    const [smooth, SmoothRange] = useRange(2, 0, 8, "Chaikin smooth iterations");
    const [simplfy, SimplifyRange] = useRange(0, 0, 100, "Simplify Cooficiant");
    const [pointDeviation, [pdx, pdy]] = usePoint({ x: 25, y: 88 }, 0, 2000, "Point deviation");
    const [spointDeviation, [spdx, spdy]] = usePoint({ x: 283, y: 165 }, 0, 2000, "Sub-Point deviation");

    return (
        <>
            <LineCanvas size={{
                x: 1200,
                y: 800,
            }}
                lines={[...generateFullLines(350, 0, "#fcba03"), ...generateFullLines(800 - 350, -350, "#03a1fc"), ...generateFullLines(250, -350, "#eaff00", 400, 'soft-light')]}
                lineCount={lineCount}
                smooth={smooth}
                simplfy={simplfy}
                pointDeviation={pointDeviation}
                spointDeviation={spointDeviation}
            />
            <Row>
                <Col span={8}>
                    <Range {...LineRange} />
                </Col>
                <Col span={8}>
                    <Range {...SmoothRange} />
                </Col>
                <Col span={8}>
                    <Range {...SimplifyRange} />
                </Col>
                <Col span={8}>
                    <Range {...pdx} />
                    <Range {...pdy} />
                </Col>
                <Col span={8}>
                    <Range {...spdx} />
                    <Range {...spdy} />
                </Col>
            </Row>
        </>
    )
}

export default SunPage