import React, { PropsWithChildren } from "react";

interface NWContext {
  selectedWorld: string;
  isWorldSelected: boolean;
}

interface NWProvider extends NWContext {
  setSelectedWorld: (selectedWorld: string) => void;
}

export const NewWorldContext = React.createContext<NWProvider>(
  {} as NWProvider
);

export const NewWorldContextProvider: React.FC<PropsWithChildren> = ({
  children,
}) => {
  const [state, setState] = React.useState<NWContext>({
    selectedWorld: "",
    isWorldSelected: false,
  });

  const handleWorldChange = (selectedWorld: string) => {
    setState({
      selectedWorld,
      isWorldSelected: true,
    });
  };

  return (
    <NewWorldContext.Provider
      value={{
        setSelectedWorld: handleWorldChange,
        ...state,
      }}
    >
      {children}
    </NewWorldContext.Provider>
  );
};

export const useNewWorldContext = () => {
  return React.useContext(NewWorldContext);
};
