import { Typography } from "antd";
import React from "react";
import { useWorlds } from "../../hooks/useWorlds";
import { useNewWorldContext } from "../new-world-context";

export const WorldList: React.FC = () => {
  const { active, merged } = useWorlds();
  const { setSelectedWorld, selectedWorld } = useNewWorldContext();

  return (
    <div className="flex justify-around mb-2">
      {active.map((world) => (
        <div
          className="cursor-pointer"
          key={world.nw_id}
          onClick={() => setSelectedWorld(world.nw_id)}
        >
          <Typography
            className={`${selectedWorld === world.nw_id ? "!font-bold" : ""}`}
          >
            {world.name}
            <sup className="ml-1 ">({world.region})</sup>
          </Typography>
        </div>
      ))}
      <Typography className="!text-darkerb">|</Typography>
      {merged.map((world) => (
        <div
          className="cursor-pointer"
          key={world.nw_id}
          onClick={() => setSelectedWorld(world.nw_id)}
        >
          <Typography
            className={`!text-darkerb ${
              selectedWorld === world.nw_id ? "!font-bold" : ""
            }`}
          >
            {world.name}
            <sup className="ml-1 ">({world.region})</sup>
          </Typography>
        </div>
      ))}
    </div>
  );
};
